*{
    padding: 0;
    margin: 0;
}
body{
    background-color: rgb(0, 10, 32);
    color: #fff;
    font-family: monospace;
    transition: all .3s;
    .container{
        width: 1140px;
        margin: 0 auto;
    }
    header{
        padding: 20px;
        margin-top: 50px;
        .container{
            display: flex;
            justify-content: space-between;
            align-self: center;
            h1{
                color: gold;
                font-weight: bold;
                font-size: 35px;
            }
            select{
                width: 100px;
                height: 50px;
                margin: 0px 10px;
                font-size: 20px;
                background: gold;
                color: black;
                font-weight: bold;
                outline: none;
                border: none;
                border-radius: 10px;

            }
        }
    }
    
    main{
        margin-top: 50px;
        h3{
            font-size: 25px;
        }
    }

    footer{
        padding: 20px;
        margin-top: 50px;
        .container{
            h1{
                color: gold;
                font-weight: bold;
                font-size: 35px;
            }
            button{
                padding: 20px 40px;
                background: none;
                border: none;
                background-color: rgb(0, 10, 32);
                border: 3px solid #fff;
                border-radius: 3px;
                margin: 0 30px;
                margin-top: 30px;
                margin-left: 0;
                color: #fff;
                font-size: 20px;
                font-weight: bold;
                transition: all .5s;
                &:last-child{
                    background-color: gold;
                    color: #000;
                }&:hover{
                    color: rgb(0, 10, 32);
                    background-color: #fff;
                    cursor: pointer;
                }
            }
        }
    }
}